.pt-10{
    padding-top: 6rem !important;
}
label{
    margin-bottom: unset;
}
.bg-light {
    background-color: #FDFDFD !important;
}
.bg-grey {
    background-color: #F1F0F0;
}
.text-orange,.MuiTypography-colorTextSecondary{
    color: #F27016 !important;
}
.text-grey{
    color : #A3A3A3! important;
}
.text-darkgrey {
    color: #272727 !important;
}
a:hover,a:focus,a{
    color: #000 !important;
    text-decoration: unset !important;
}
.border-b-1{
    border-bottom: 1px solid rgb(242, 242, 242);
}
.orange-btn:hover{
    background-color: #E85A19 !important;
    color : white;
}
.btn-black:hover{
    background-color: #152B39 !important;
    color : white;
}

.w-30px{
    min-width : 30px !important;
}
.btn-orange-gradiant{
    background-image: linear-gradient(to left, #F5CE62 , #E85A19) !important;
    color : white !important;
    border: none !important;
    box-shadow: none !important;
}
.btn-white-gradiant{
    background-image: white;
    border : 0 !important;
    color   :#E85A19 !important;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 2px 10px 0px, rgba(0, 0, 0, 0.24) 0px 0px 1px 0px !important;
}
.btn-orange-gradiant:hover{
    background-image: linear-gradient(to left, rgb(247, 189, 31) , rgb(245, 78, 0)) !important;
}
.btn-orange-outline-gradiant:hover{
    background-image: linear-gradient(to left, #F5CE62 , #E85A19) !important;
    color : white !important;
    border-color :  white !important;
}
.dividor-container {
    width: 100%; 
    text-align: center; 
    border-bottom: 1px solid #f2f2f2; 
    color: #919191;
    line-height: 0.1em;
    margin: 10px 0 20px; 
 } 
 
 .dividor-container span { 
    background: #fdfdfd;
    padding: 0 30px;
 }
 .box-shadow-none{
    box-shadow: none !important;
}
/* .box-shadow-none:focus,.box-shadow-none:hover,.box-shadow-none:active{
    box-shadow: none !important;
    border : 0 !important;
    background-color: unset !important;
} */

.btn-google{
    border : none !important;
    /* box-shadow: none !important; */
    box-shadow: rgba(0, 0, 0, 0.1) 0px 2px 10px 0px, rgba(0, 0, 0, 0.24) 0px 0px 1px 0px !important;

}
.container-custom{
    padding: 0 5rem;
}
.card-img-overlay {
    top: unset;
    bottom: 34px;
    padding: 1.25rem 0.3rem;
}
.underline{
    text-decoration: underline !important;
    text-underline-position: under;
}
h6{
    font-weight: 420 !important;
}
.h7, h7 {
    font-size: 0.9rem;
}
.MuiPaper-elevation1,.MuiPaper-elevation3,.MuiPaper-elevation2,.MuiPaper-elevation4,.shadow{
    box-shadow: rgba(0, 0, 0, 0.09) 0px 2px 20px 0px, rgba(0, 0, 0, 0.09) 0px 0px 20px 0px !important;
    border: none;
}
.MuiListItemSecondaryAction-root{
    right: 4px !important;
}
.MuiListItemAvatar-root{
    min-width: 45px !important;
}
.btn-bookmark:hover,.btn-bookmark:focus{
    content: url(./components/card-interview/images/yellow-bookmark.png);
}
.MuiTypography-body2{
    font-weight: 500;
}
@media only screen and (max-width: 600px) {
    .side-bg {
        background-size: inherit;
        background-position: center;
        background-color: #c6c5c3;
        text-align: center !important;
        height: auto;
    }
    .side-bg .h1{
        font-size: 1.75rem;
    }
    .side-bg .side-container{
        padding-left: 15px !important;
        text-align: center !important;
        background-color: #4e4e4e3d;
        padding-bottom: 0 !important;
        padding-top: 1.8rem !important;
    }
    .side-bg br {
        display: none;
        }
    .side-bg  .h1, h1 {
        font-size: 1.5rem;
    }
    .pt-10{
        padding: 1rem 0rem !important;
    }
    .MuiTypography-h4 {
        font-size: 1.5rem !important;
    }
    .MuiTypography-body1{
        font-size: 12px !important;
    }
    .online-avatar{
        display: none !important;
    }
    .text-xs-center{
        text-align: center !important;
    }
    .btn-white-gradiant{
        width: 100%;
    }
    .container-custom{
        padding-right: 15px;
        padding-left: 15px;
    }
    .side-container .pr-5 {
        padding-right: 0 !important;
    }
    .text-center-sm{
        text-align: center !important;
    }
    .w-50-sm{
        width: 50% !important;
    }
  }
